import { Record, Union } from "./fable_modules/fable-library.3.7.20/Types.js";
import { int32_type, record_type, list_type, string_type, union_type } from "./fable_modules/fable-library.3.7.20/Reflection.js";
import { ofArray, singleton } from "./fable_modules/fable-library.3.7.20/List.js";

export class MagicType extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Clerical", "Wizardly", "Druidic"];
    }
}

export function MagicType$reflection() {
    return union_type("Spells.MagicType", [], MagicType, () => [[], [], []]);
}

export class Prereq extends Record {
    constructor(heading, items) {
        super();
        this.heading = heading;
        this.items = items;
    }
}

export function Prereq$reflection() {
    return record_type("Spells.Prereq", [], Prereq, () => [["heading", MagicType$reflection()], ["items", list_type(list_type(string_type))]]);
}

export class Spell extends Record {
    constructor(name, college, prereqs, page) {
        super();
        this.name = name;
        this.college = college;
        this.prereqs = prereqs;
        this.page = (page | 0);
    }
}

export function Spell$reflection() {
    return record_type("Spells.Spell", [], Spell, () => [["name", string_type], ["college", string_type], ["prereqs", list_type(Prereq$reflection())], ["page", int32_type]]);
}

